import qs from 'qs'
import Login from '../../common/login.js'
import opBaidu from '../../components/w-pay/op-baidu.vue'
import opH5 from '../../components/w-pay/op-h5.vue'
export default {
	data() {
		return {
			mobile: '',
			orderList: [],
			userInfo: null,
			isEmpty: false,
			refresh: 0
		};
	},
	components: {
		opBaidu,
		opH5
	},
	onShow() {





















	},
	onLoad() {
		this.getUserInfo()
	},
	methods: {
		refreshHandler(){
			this.refresh += 360
			this.getUserInfo()
		},
		call() {
			console.log('call 18163728777')
			uni.makePhoneCall({
				phoneNumber: '18163728777'
			});
		},
		getUserInfo() {
			if (uni.getStorageSync('userInfo')) {
				this.userInfo = uni.getStorageSync('userInfo')
				uni.showLoading({
					title: '查询中'
				});
				uni.request({
					url: this.$store.state.baseUrl + '/api/child/child_order_query',
					method: 'POST',
					data: {
						app_mchid: uni.getStorageSync('token'),
						app: 1
					},
					header: {
						"Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
					},
					success: (res) => {
						uni.hideLoading()
						if (res.data.code == 1) {
							this.orderList = res.data.data
							if (this.orderList.length == 0) {
								this.isEmpty = true
							}
						} else {
							uni.showToast({
								title: res.data.msg,
								icon: 'none'
							})
						}
					}
				});
			}
		},
		loginAuth(obj) {
			console.log(obj)
			Login().than(() => {
				this.getUserInfo()
			})
		},
		search() {
			this.orderList = null
			if (this.mobile == '') {
				uni.showToast({
					title: '请输入手机号码',
					icon: 'none'
				})
			} else if (this.mobile.length != 11) {
				uni.showToast({
					title: '手机号格式不正确',
					icon: 'none'
				})
			} else {
				uni.showLoading({
					title: '查询中'
				});
				uni.request({
					url: this.$store.state.baseUrl + '/api/nameorder/order_query',
					method: 'POST',
					data: {
						mobile: this.mobile,
						app: 1
					},
					header: {
						"Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
					},
					success: (res) => {
						uni.hideLoading()
						if (res.data.data.length > 0) {
							this.orderList = res.data.data
						} else {
							uni.showToast({
								title: '未查询相关到信息',
								icon: 'none'
							})
						}
					}
				});
			}
		},
		orderNext(i) {
			let params = {
				name_total: this.orderList[i].level_num,
				order_sn: this.orderList[i].order_sn
			}
			uni.navigateTo({
				url: './list?' + qs.stringify(params)
			});

		}
	}
}
